.detail {
  margin-top: 180px;
  &__header {
    &-name {
      font-size: 25px;
      padding: 10px 0;
      display: none;
      @include Responsive(MD) {
        display: block;
      }
    }
    &-info {
      display: none;
      justify-content: space-between;
      padding: 10px 0;
      div:nth-child(1) {
        text-decoration: underline;
      }
      div:nth-child(2) {
        display: flex;

        p {
          margin: 0 5px;
          padding: 5px 10px;
          border-radius: 10px;
          &:hover {
            background-color: rgb(234, 230, 230);
          }
        }
      }

      @include Responsive(MD) {
        display: flex;
      }
    }

    &-img {
      width: 100%;
      overflow: hidden;
      position: relative;
      img {
        object-fit: cover;
        object-position: 0% 60%;
        overflow: hidden;
        position: relative;
        @include Responsive(MD) {
          width: 100%;
        }
      }
      .detail__goback {
        position: absolute;
        top: 20px;
        left: 20px;
        color: black;
        background-color: white;
        padding: 10px;
        border-radius: 50%;
        @include Responsive(MD) {
          display: none;
        }
      }
    }
  }
  &__body {
    display: flex;
    flex-direction: column;
    margin: 40px 20px;
    @include Responsive(MD) {
      flex-direction: row;
      margin: 40px 0;
    }
    &-left {
      flex: 2.5;

      @include Responsive(MD) {
        padding-right: 40px;
      }
      h2 {
        padding: 10px 0;
        font-size: 20px;
        font-weight: 500;
      }

      hr {
        height: 1px;
        width: 100%;
        background-color: $border;
        margin: 40px 0;
      }
      ul {
        li {
          display: flex;
          align-items: center;
          padding: 10px 0;
          h3 {
            font-weight: 600;
          }
          p {
            padding: 5px 0;
          }
        }
      }
      &-icon {
        font-size: 20px;
        margin-right: 10px;
      }
      &-desc {
        p {
          padding: 20px 0;
        }
        h3 {
          text-decoration: underline;
          font-weight: 600;
          font-size: 16px;
          cursor: pointer;
        }
      }
      .translate-desc {
        p {
          padding: 10px 0;
        }
        span {
          font-weight: 600;
          font-size: 16px;
          text-decoration: underline;
        }
      }
      .feature {
        div {
          display: flex;

          ul {
            padding-right: 40px;
            li {
              font-size: 16px;
              font-weight: 400;
              .icon {
                font-size: 20px;
                margin-right: 10px;
              }
            }
          }
        }
        &-display {
          margin: 20px 0;
          button {
            width: 100%;
            padding: 15px 30px;
            background-color: transparent;
            border: 1px solid #333;
            border-radius: 10px;
            font-weight: 500;
            font-size: 16px;
            &:hover {
              background-color: $hoverColor;
            }
          }
        }
      }
      .date-range-picker {
        display: none;
        @include Responsive(MD) {
          display: flex;
        }
      }
      .date-range {
        width: 100%;
        .rdrMonth {
          width: 100%;
        }
        @include Responsive(MD) {
          display: none;
        }
      }
    }
    &-right {
      flex: 1;
      position: relative;
      display: none;
      @include Responsive(MD) {
        display: block;
      }
      &-wrapper {
        position: sticky;
        top: 150px;
        padding: 20px;
        box-shadow: 1px -1px 5px 2px rgba(189, 189, 189, 0.38);
        -webkit-box-shadow: 1px -1px 5px 2px rgba(189, 189, 189, 0.38);
        -moz-box-shadow: 1px -1px 5px 2px rgba(189, 189, 189, 0.38);
        border: 1px solid $border;
        border-radius: 10px;
        .header {
          display: flex;
        }
        .date {
          margin: 20px 0;
          border: 1px solid $border;
          border-radius: 8px;
          position: relative;
          &__header {
            display: flex;
            position: relative;
            width: 100%;
            div:nth-child(1) {
              border-right: 1px solid $border;
            }
            div {
              border-bottom: 1px solid $border;
              padding: 10px;
              p {
                font-weight: 500;
              }
              input {
                width: 100%;
              }
            }

            .date-picker {
              display: none;
              background-color: #fff;
              position: absolute;
              right: 0;
              padding: 0;
              top: 0;
              z-index: 100;
              box-shadow: 1px -1px 5px 2px rgba(189, 189, 189, 0.38);
              -webkit-box-shadow: 1px -1px 5px 2px rgba(189, 189, 189, 0.38);
              -moz-box-shadow: 1px -1px 5px 2px rgba(189, 189, 189, 0.38);

              .date-range-picker {
                display: none;
                @include Responsive(MD) {
                  display: block;
                }
              }

              &.active {
                display: block;
              }

              .btn-close {
                display: flex;
                justify-content: flex-end;
                padding: 10px 20px;
                text-decoration: underline;
                font-size: 16px;
                font-weight: 600;

                &:hover {
                  cursor: pointer;
                }
              }
            }
          }
          &__bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;

            div {
              padding: 10px;
              p {
                font-weight: 500;
              }
            }
            &-submenu {
              &.active {
                display: block;
              }
              display: none;
              position: absolute;
              background-color: white;
              z-index: 99;
              top: 100%;
              box-shadow: 1px -1px 5px 2px rgba(189, 189, 189, 0.38);
              -webkit-box-shadow: 1px -1px 5px 2px rgba(189, 189, 189, 0.38);
              -moz-box-shadow: 1px -1px 5px 2px rgba(189, 189, 189, 0.38);
              ul {
                padding: 10px;
                li {
                  display: flex;
                  align-items: center;
                  justify-content: space-between;
                  padding: 20px 0;
                  h2 {
                    font-size: 16px;
                  }
                  span {
                    border: 1px solid $border;
                    padding: 10px 15px;
                    margin: 0 10px;
                    border-radius: 50%;
                  }
                  &:hover {
                    text-decoration: none;
                  }
                }
              }
              .btn-close {
                display: flex;
                justify-content: flex-end;
                padding: 10px 20px;
                text-decoration: underline;
                font-size: 16px;
                font-weight: 600;

                &:hover {
                  cursor: pointer;
                }
              }
            }
          }
        }
        .checkRoom {
          background: linear-gradient(
            to right,
            rgb(230, 30, 77) 0%,
            rgb(227, 28, 95) 50%,
            rgb(215, 4, 102) 100%
          );
          color: white;
          width: 100%;
          padding: 15px 0;
          border-radius: 10px;
        }
        .price {
          span {
            display: block;
            text-align: center;
            color: $textSub;
            padding: 10px 0;
          }
          div {
            display: flex;
            justify-content: space-between;
            padding: 10px 0;

            p {
              color: $textSub;
              font-size: 16px;
            }
          }
        }
        .bottom {
          display: flex;
          justify-content: space-between;
          padding: 20px 0;
          p {
            font-size: 18px;
            font-weight: 600;
          }
        }
      }
    }
  }

  .detail__review {
    margin-top: 40px;
    &-header {
      span {
        font-size: 20px;
        margin-right: 20px;
      }
      h1 {
        font-size: 25px;
        font-weight: 500;
      }
      p {
        color: $textSub;
        padding: 20px 0;
      }
    }
    &-body {
      padding: 20px 0;
      &-item {
        .wrapper {
          display: flex;
          align-items: center;
          div {
            margin: 0 20px;
            p {
              color: $textSub;
            }
          }
          img {
            width: 50px;
            height: 50px;
            border-radius: 50%;
            object-fit: cover;
          }
        }
        .content {
          font-size: 18px;
          padding: 10px 0;
        }
      }
    }
    .post-comment {
      display: flex;
      flex-direction: column;
      width: 100%;
      @include Responsive(MD) {
        width: 400px;
      }
      .star-rating {
        text-align: right;
        margin: 10px 0;

        .star {
          margin: 0 5px;
          &-icon {
            font-size: 18px;
          }
        }
        button {
          background-color: transparent;
          border: none;
          outline: none;
          cursor: pointer;
        }
        .on {
          color: rgb(187, 187, 26);
        }
        .off {
          color: #ccc;
        }
      }

      textarea {
        border: 1px solid $border;
        padding: 10px 20px;
        box-shadow: $box-shadow;
      }
      .btn-comment {
        width: 100px;
        padding: 10px 20px;
        margin: 20px 0;
        box-shadow: $box-shadow;
        &:active {
          @include Active-button;
        }
        &:hover {
          background: $hoverColor;
        }
      }
    }
  }
}
