.add__room__modal {
  .form {
    width: 100%;
    .form-group-container {
      display: flex;
      .form-group-wrapper {
        flex: 2;
        .form-group {
          margin: 20px 0;
          position: relative;
          p {
            margin: 5px 0;
          }
          input {
            border: 1px solid $border;
            padding: 10px 20px;
            width: 100%;
            border-radius: 8px;
          }
          textarea {
            border: 1px solid $border;
            padding: 10px 20px;
            width: 100%;
            border-radius: 8px;
          }
          .gender {
            display: flex;
          }
          .error {
            color: red;
            font-size: 12px;
            position: absolute;
          }
          .password {
            position: relative;
            span {
              position: absolute;
              top: 50%;
              right: 20px;
              transform: translateY(-30%);
              .password-icon {
                font-size: 18px;
              }
            }
          }
        }
      }

      .form-group-checkbox-container {
        flex: 1;
        margin-left: 10px;

        .form-group-checkbox {
          .form-checkbox {
            opacity: 0;
            position: relative;
            &::before {
              position: absolute;

              opacity: 1;
              content: "";
              bottom: 100%;
              background-color: blue;
              width: 100px;
              height: 40px;
            }
          }
          p {
            color: white;
            background-color: gray;
            text-align: center;
            padding: 10px 0;
            border-radius: 50px;

            &.active {
              background: $mainColor;
            }
          }
        }
      }
    }

    .form-button {
      display: flex;
      align-items: center;
      p {
        flex: 1;
        color: red;
        &:hover {
          text-decoration: underline;
          cursor: pointer;
        }
      }
      .login-button {
        flex: 2;
        background: linear-gradient(
          to right,
          rgb(230, 30, 77) 0%,
          rgb(227, 28, 95) 50%,
          rgb(215, 4, 102) 100%
        );
        color: white;
        width: 100%;
        padding: 15px 0;
        border-radius: 10px;
      }
    }
    .form-bottom {
      padding-top: 20px;
      text-align: center;
      span {
        a {
          color: red;
        }
        &:hover {
          text-decoration: underline;
          cursor: pointer;
        }
      }
    }
  }
}
