.language {
  @include Responsive(MD) {
    width: 400px;
  }
  h1 {
    font-size: 25px;
    padding: 20px 0;
  }
  ul {
    li {
      padding: 10px 20px;
      margin: 10px 0;
      border: 1px solid $border;
      font-size: 16px;
      border-radius: 10px;
      &.active {
        border: 1px solid $mainColor;
      }
      &:hover {
        background-color: $mainColor;
        color: white;
        transition: all 0.3s ease-in-out;
        text-decoration: none;
      }
      span {
        color: $textSub;
      }
    }
  }
}
